@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
@font-face {
  font-family: "Waltograph";
  font-style: normal;
  src: url(/recette-manager/static/media/waltographUI.ef6f8e59.ttf);
}

body {
  margin: 0;
  text-align: center;
  font-family: "Open Sans", sans-serif;

  background: #4d4d4d;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-bar {
  list-style: none;
  margin: 0 auto;
  padding: 0 auto;
  width: 80%;
  height: 4rem;
  border-radius: 1.5rem;
  display: flex;
  justify-content: flex-start;

  align-items: center;
  align-content: center;
  background: white;
  overflow: hidden;
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}

.nav-bar li {
  width: 100%;
  height: 100%;
}

.nav-bar a {
  width: 100%;
  height: 100%;
  border: 1px solid transparent;
  color: #4d4d4d;
  text-decoration: none;
  display: block;
  font-size: 1.5rem;
}

.nav-bar a:hover,
.nav-bar a:active,
.nav-bar a.active {
  background: #4d4d4d;
  border-color: #4d4d4d;
  color: white;
}

.nav-bar button {
  cursor: pointer;
  border: 1px solid white;
  color: white;
  background: transparent;
  padding: 0.5rem;
  font: inherit;
}

.nav-bar button:focus {
  outline: none;
}

.nav-bar button:hover,
.nav-bar button:active {
  background: #f8df00;
  color: #292929;
}

.main-navigation {
  position: fixed;
  width: 100%;
  top: 0;
}

.bouton {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #ff0055;
  border-radius: 4px;
  background: #ff0055;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.bouton:focus {
  outline: none;
}
.bouton:hover,
.bouton:active {
  background: #ff4382;
  border-color: #ff4382;
}

.bouton--inverse {
  background: transparent;
  color: #ff0055;
}

.bouton--inverse:hover,
.bouton--inverse:active {
  color: white;
  background: #ff0055;
}

.bouton--danger {
  background: #830000;
  border-color: #830000;
}

.bouton--danger:hover,
.bouton--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.bouton:disabled,
.bouton:hover:disabled,
.bouton:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.bouton--small {
  font-size: 0.8rem;
}

.bouton--big {
  font-size: 1.5rem;
}

.header-nav-bar {
  color: white;
  font-size: 3rem;
  font-family: "waltograph";
  width: 100%;
  height: 5rem;
  padding: 1rem;
  background: #4d4d4d;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.header-nav-bar p {
  text-align: center;
}

.card {
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 4rem;
  padding: 0;
  overflow: hidden;
  background: white;
}

.recette-item {
  margin: 4rem auto;
  width: 90%;
  max-width: 50rem;
  list-style: none;
}

.recette-item__header {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 1rem;
  border-bottom: solid 1px grey;
  color: white;
  background: #ff0055;
}
.recette-item__header h1 {
  -webkit-margin-start: 0;
          margin-inline-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
}
.recette-item__corps {
  display: flex;
  flex-direction: row;
  margin-top: 2rem;
  border-bottom: solid 1px grey;
  padding-bottom: 2rem;
}
.recette-item__image {
  width: 75%;
}
.recette-item__image img {
  width: 100%;
  height: 100%;
  border: solid transparent 2px;
  border-radius: 4rem;
  object-fit: cover;
}
.recette-item__info {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.recette-item__description {
  width: 100%;
  height: inherit;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-content: center;
  align-self: center;
}
.recette-item__description span {
  width: 90%;
  height: 100%;
}

.recette-item__tags {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
}
.recette-item__tags div {
  width: 41%;
  height: 2rem;
  color: green;
  border: solid 1px green;
  border-radius: 0.9rem;
  margin: 0.5rem;

  padding: auto;
}
.recette-item__tags div:hover {
  color: white;
  border: solid 1px white;
  background-color: green;
  border-radius: 0.9rem;
}
.recette-item__footer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 1rem;
}


.main-area {
  margin-top: 15rem;
}

.App {
  text-align: center;
}

